import { players } from "@/app/type/tables/tables"; // Import the array
import { notFound } from "next/navigation";

interface PlayerPageProps {
  params: { id: string }; // Next.js automatically provides `params`
}

const PlayerPage: React.FC<PlayerPageProps> = ({ params }) => {
  const player = players.find((p) => p.id === params.id);

  if (!player) {
    return notFound(); // Show 404 if the player is not found
  }

  return (
    <div>
      <h1>Player Details</h1>
      <p>ID: {player.id}</p>
      <p>Name: {player.name}</p>
    </div>
  );
};

export default PlayerPage;
